.input {
  border: 1px solid #cccccc;
  border-radius: 3px;
  background: #ffffff;
  outline: none;
  height: 40px;
  min-width: 250px;
  color: #cccccc;
  font-size: 16px;
  font-family: Tahoma;
  margin-right: 20px;
  margin-left: 20px;
}

.input:focus {
    border: 1px solid blue;
}

input::placeholder {
    color: black;
    font-size: 16px;
    font-style: italic;
    font-family: Tahoma;
  }

.button {
    background-color: silver; 
    border-radius: 4px;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}
.span {
    font-family: 'Courier New', Courier, monospace;
    font-size: 24px;
    font-weight: 700;

}