.button {
  background-color: silver; 
  border-radius: 4px;
  border: none;
  color: white;
  padding: 12px 28px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  margin-left: 10px;
}

.contact{
  display: flex;
}

.list {
list-style: none;
}

.item {
  display: flex;
  align-items: center;
  justify-content: center;
}
.item:not(:last-child) {
  margin-bottom: 10px;
}

.span {
 font-size: 16px;
 font-family: Verdana, Geneva, Tahoma, sans-serif;
 font-weight: 700;
 
}