.input {
    border: 1px solid #cccccc;
    border-radius: 3px;
    background: #ffffff;
    outline: none;
    height: 40px;
    min-width: 250px;
    color: #cccccc;
    font-size: 16px;
    font-family: Tahoma;
    margin-right: 20px;
    margin-left: 20px;
  }
  .input:focus {
    border: 1px solid blue;
}